<script>
import {Table, TableColumn} from 'element-ui'
import axios from "axios";
import {baseUrl} from "../../../config/index.json";

export default {
  name: 'light-table',
  components: {
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  data() {
    return {
      menus:[],
      rows: 1,
      perPage: 1,
      currentPage: 1,
      index: 0,
      right_t: false,
      data_main: [],
      title:'',
      titleru:'',
      titleen:'',
      order:'',
      isFooter:false,
      url:'',
      isButton:true,
      main_id:'',
      address:'',
      email_address:'',
      phone_number:'',
      telegram:'',
      instagram:'',
      facebook:'',
      contact:[],
      imageUrl:[],
      myImages:[],
      files_name:[],
      description:'',
      descriptionru:'',
      descriptionen:''
    };
  },
  methods: {
    trySubmitFile(e) {
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    rigt_side() {
      this.right_t = !this.right_t
      this.isButton=false
    },
    updatePage(item) {
      this.data_main = Users[item - 1]
    },
    createOz(){
      const form = new FormData();
      form.append("icon", this.myImages[0]);
      form.append("order", this.order);
      form.append("description", this.description);
      form.append("title", this.title);
      axios.post(`${baseUrl}services/`,form,
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Accept-Language':'oz'
          }
        })
        .then(res=>{
          this.myImages=[]
          this.imageUrl=[]
          this.files_name=[]
          this.title=''
          this.description=''
          this.order=''
          this.myImages=[]
          this.$toast("Ma'lumotlar o'zbek tilida kiritildi", {
            timeout: 2000,
            type:"success"
          });
          //ru
          form.append("description", this.descriptionru);
          form.append("title", this.titleru);
          axios.patch(`${baseUrl}services/${res.data.id}/`,form,
            {
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('token')}`,
                'Accept-Language':'ru'
              }
            })
            .then(()=>{
              this.titleru=''
              this.descriptionru=''
              this.order=''
              this.myImages=[]
              this.$toast("Ma'lumotlar rus tilida kiritildi", {
                timeout: 2000,
                type:"success"
              });
              //en
              form.append("description", this.descriptionen);
              form.append("title", this.titleen);
              axios.patch(`${baseUrl}services/${res.data.id}/`,form,
                {
                  headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    'Accept-Language':'kar'
                  }
                })
                .then(()=>{
                  this.titleen=''
                  this.descriptionen=''
                  this.order=''
                  this.myImages=[]
                  this.$toast("Ma'lumotlar barcha tillarda kiritildi", {
                    timeout: 2000,
                    type:"success"
                  });
                })
            })
        })
      .catch(()=>{
        this.$toast("Ma'lumot yoki logo kiritilmagan", {
          timeout: 2000,
          type:"error"
        });
      })
    },

  },
  mounted() {

  }
}

